}

.mobile-margin {
    margin: 0;
    padding: 1.5rem 1.5rem;
}

.mobile-title-margin {
    margin-left: 1.5rem;
}

.mobile-background {
    background-color: #070724;
    position: absolute;
    top: 0rem;
    left: -68rem;
    z-index: 1;
}

.mobile-hover:hover {
    background-color: #7a7a7a3b;
    transition: .15s ease-in-out;
    padding-left: .5rem;
}

.mobile-chevron {
    background-image: url(/planets-fact-site/static/media/icon-chevron.8915cee9.svg);
