.navbar-collapse {
    margin-left: auto!important;
    margin-right: 0!important;
}

.row {
    margin: 0 1.5rem!important;
    padding: 0!important;
    width: 100%!important;
    justify-content: space-between!important;
}

.logo {
    margin: 0;
    padding: .813rem 0;
    padding-left: 0!important;
}

.hamburger {
    background-image: url(/planets-fact-site/static/media/icon-hamburger.05a40a49.svg);
    width: 1.5rem!important;
    height: 1.063rem!important;
    transition: .15s;
}

.hamburger-active {
    background-image: url(/planets-fact-site/static/media/icon-hamburger-active.455fe153.svg);
    width: 1.5rem!important;
    min-height: 1.063rem!important;
    transition: .15s;
}


.hamburger:hover {
    cursor: pointer;
}

.hamburger-active:hover {
    cursor: pointer;
}

@media(min-width: 768px) {
    .logo {
        padding: 1.357rem 0 .644rem!important;
        display: flex;
    }
    .row {
        margin: 0 2rem!important;
    }
}

.mobile-trans-on {
    animation-name: mobile-trans-in;
    animation-duration: .25s;
    animation-fill-mode: forwards;
}

.mobile-trans-off {
    animation-name: mobile-trans-out;
    animation-duration: .25s;
    animation-fill-mode: forwards;
}

@keyframes mobile-trans-in {
    from {left: -68rem;}
    to {left: 0rem;}
}

@keyframes mobile-trans-out {
    from {left: 0rem;}
    to {left: -68rem;}
}
